import React, { useState } from 'react';
import { Grid, Container, Typography, Box, TextField, Button, Table, TableBody, TableRow, TableCell } from "@mui/material";

function SingleWordMode() {
  const [word, setWord] = useState("");
  const [searchResults, setSearchResults] = useState([]);
  const [wordMeaning, setWordMeaning] = useState("");

  function valueChange(e) {
    setWord(e.target.value);
  }

  function fetchWord() {
    // Fetch search results from API
    fetch(`http://127.0.0.1:8000/SingleWordMode/?word=${word}`)
      .then(res => res.json())
      .then(data => {
        console.log(data);
        const sentences = data.sentence.map((sentence, index) => ({
          sentence: sentence,
          translation: data.translation[index],
          wordMeaning: data.word_means[index]
        }));
        setSearchResults(sentences);
        setWordMeaning(sentences.length > 0 ? sentences[0].wordMeaning: "");
      })
      .catch(error => console.error('Error fetching data:', error));
  }

  function clearWord() {
    setWord("");
    setSearchResults([]);
    setWordMeaning("");
  }



  return (
    
      <Container>
        <Grid container spacing={3}>
          {/* Title */}
          
          {/* Search Bar */}
          <Grid item xs={12} md={6}>
            <TextField
              id="word"
              label="Word"
              variant="outlined"
              onChange={valueChange}
              value={word}
              fullWidth
            />
          </Grid>
          <Grid item xs={6} sm={3} md={3}>
            <Button variant='contained' onClick={fetchWord} fullWidth style={{ backgroundColor: '#4CAF50', color: '#FFF' }}>Search</Button>
          </Grid>
          <Grid item xs={6} sm={3} md={3}>
            <Button variant='contained' onClick={clearWord} fullWidth style={{ backgroundColor: '#F44336', color: '#FFF' }}>Clear</Button>
          </Grid>
          {/* Meaning */}
          <Grid item xs={12}>
            <Box sx={{ backgroundColor: "#FFF", padding: "20px", borderRadius: "5px", boxShadow: "0 4px 8px rgba(0, 0, 0, 0.1)" }}>
              <Typography variant="h5" style={{ color: '#333', marginBottom: '20px', borderBottom: '1px solid #ddd', paddingBottom: '10px' }}>Meaning</Typography>
              {searchResults.length > 0 ? (
                <Typography variant="body1" style={{ fontSize: '1.2rem', color: '#4CAF50' }}>{wordMeaning}</Typography>
              ) : (
                <Typography variant="body1" style={{ color: '#666' }}>No results found.</Typography>
              )}
            </Box>
          </Grid>
          {/* Example Sentences */}
          <Grid item xs={12}>
            <Box sx={{ backgroundColor: "#FFF", padding: "20px", borderRadius: "5px", boxShadow: "0 4px 8px rgba(0, 0, 0, 0.1)" }}>
              <Typography variant="h5" style={{ color: '#333', marginBottom: '20px', borderBottom: '1px solid #ddd', paddingBottom: '10px' }}>Example Sentences</Typography>
              {searchResults.length > 0 ? (
                <Table>
                  <TableBody>
                    {searchResults.map((result, index) => (
                      <TableRow key={index}>
                        <TableCell>{index + 1}</TableCell>
                        <TableCell>{result.sentence}</TableCell>
                        <TableCell>{result.translation}</TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              ) : (
                <Typography variant="body1" style={{ color: '#666' }}>No results found.</Typography>
              )}
            </Box>
          </Grid>
          {/* Notice section */}
          <Grid item xs={12}>
            <Box sx={{ backgroundColor: "#FFF", padding: "20px", borderRadius: "5px", boxShadow: "0 4px 8px rgba(0, 0, 0, 0.1)", marginTop: '20px' }}>
              <Typography variant="h5" style={{ color: 'red' }}>Notice</Typography>
              <Typography variant="body1" style={{ color: 'red' }}>!      !</Typography>
              <Typography variant="body1" style={{ color: 'red' }}>caution! Words that are too easy won't come up even if you search for them!</Typography>
            </Box>
          </Grid>
        </Grid>
      </Container>

  );
}

export default SingleWordMode;
