import React, { useState } from 'react';
import { Grid, Container, Typography, Box, TextField, Button } from "@mui/material";
import SingleWordMode from "./SingleWordMode";
import MultipleWordsMode from "./MultipleWordsMode";

function App() {
  const [mode, setMode] = useState("single");

  const handleModeChange = (selectedMode) => {
    setMode(selectedMode);
  };

  return (
    <Container>
      <Grid container spacing={3}>
        {/* Mode selection buttons */}
        <Grid item xs={12}>
          <Box sx={{ marginBottom: '20px' }}>
            <Button
              variant={mode === 'single' ? 'contained' : 'outlined'}
              onClick={() => handleModeChange('single')}
              style={{ marginRight: '10px' }}
            >
              Single Word Mode
            </Button>
            <Button
              variant={mode === 'multiple' ? 'contained' : 'outlined'}
              onClick={() => handleModeChange('multiple')}
            >
              Multiple Words Mode
            </Button>
          </Box>
        </Grid>
        {/* Render selected mode */}
        {mode === 'single' && <SingleWordMode />}
        {mode === 'multiple' && <MultipleWordsMode />}
      </Grid>
    </Container>
  );
}

export default App;
