import React, { useState } from 'react';
import { Grid, Typography, Box, Button } from "@mui/material";
import * as XLSX from 'xlsx'; // Import all exports from 'xlsx'

function MultipleWordsMode() {
  const [fileContent, setFileContent] = useState("");
  const [selectedFile, setSelectedFile] = useState(null);
  const [searchResults, setSearchResults] = useState([]);
  const handleFileChange = (e) => {
    const file = e.target.files[0];
    setSelectedFile(file);
    const reader = new FileReader();
    reader.onload = (event) => {
      setFileContent(event.target.result);
    };
    reader.readAsText(file);
  };
  const handleSubmit = async () => {
    try {
        const formData = new FormData();
        formData.append('file', selectedFile);

        const response = await fetch('http://127.0.0.1:8000/MultipleWordMode/', {
            method: 'POST',
            body: formData,
        });

        if (!response.ok) {
            throw new Error('Network response was not ok');
        }

        const jsonData = await response.json();

        const wb = XLSX.utils.book_new(); 
        const ws = XLSX.utils.json_to_sheet(jsonData); 
        XLSX.utils.book_append_sheet(wb, ws, "Sheet1"); 
        XLSX.writeFile(wb, "example.xlsx");
    } catch (error) {
        console.error('Error fetching data:', error);
    }
};
  return (
    <Grid item xs={12}>
      
      <Box sx={{ backgroundColor: "#FFF", padding: "20px", borderRadius: "5px", boxShadow: "0 4px 8px rgba(0, 0, 0, 0.1)" }}>
        <Typography variant="h5" style={{ color: '#333', marginBottom: '20px', borderBottom: '1px solid #ddd', paddingBottom: '10px' }}>Upload a text file</Typography>
        <input
          accept=".txt"
          id="contained-button-file"
          type="file"
          onChange={handleFileChange}
          style={{ display: 'none' }}
        />
        <label htmlFor="contained-button-file">
          <Button variant="contained" component="span" style={{ backgroundColor: '#4CAF50', color: '#FFF', marginRight: '10px' }}>
            Choose File
          </Button>
        </label>
        <Button variant="contained" onClick={handleSubmit} style={{ backgroundColor: '#4CAF50', color: '#FFF' }}>
          Submit
        </Button>
        {(selectedFile != null) ? (<Typography>{selectedFile.name}</Typography>) : (<></>)}
      </Box>
      {/* Display search results */}
      {searchResults.length > 0 && (
        <Box sx={{ backgroundColor: "#FFF", padding: "20px", borderRadius: "5px", boxShadow: "0 4px 8px rgba(0, 0, 0, 0.1)", marginTop: '20px' }}>
          <Typography variant="h5" style={{ color: '#333', marginBottom: '20px', borderBottom: '1px solid #ddd', paddingBottom: '10px' }}>Search Results</Typography>
          <ul>
            {searchResults.map((sentence, index) => (
              <li key={index}>{sentence}</li>
            ))}
          </ul>
        </Box>
      )}
      <Box sx={{ padding: "20px"}}>
              <Typography variant="h5" style={{ color: 'red' }}>Notice</Typography>
              <Typography variant="body1" style={{ color: 'red' }}>!     txt ,    .</Typography>
              <Typography variant="body1" style={{ color: 'red' }}>caution! The input text file must be a txt file, and words must be separated by line breaks.</Typography>
            </Box>
    </Grid>
  );
}

export default MultipleWordsMode;
